.card-creator{
  border: 1px solid #ffffff36;
  margin-bottom: 24px;
}

.card-creator .media{
  position: relative;
}
.card-creator .media img {
  height: 120px;
  width: 100%;
}
.card-creator .media video {
  height: 120px;
  width: 100%;
}

.card-creator .card-content{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: -45px;
  z-index: 10;
}

.card-creator .card-content img{
  width: 80px;
  height: 80px;
  border-radius: 50px;
}

.card-creator .card-content video{
  width: 80px;
  height: 80px;
  border-radius: 50px;
}
.card-creator .card-content .creator-name{
  font-weight: 900;
  margin: 10px 0px 16px;
}